.banner{
    background-image: url("../img/background.jpg.crdownload"); 
    background-repeat: no-repeat;
    background-size: cover;
    height:100vh;
    text-align:center;
    @include flex(
        $flex-display:flex,
        $flex-direction:column,
        $flex-wrap:wrap,
        $flex-align:normal,
        $flex-justify:space-around
        );
    margin-bottom:20px;
}



